<template>
<div>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 md12 class="pr-1">
        <v-card class="mb-2">
          <v-card-title class="headline font-weight-regular blue-grey white--text">Audit List</v-card-title>
          <v-card-text>
            <v-list>
                <v-list-tile v-for="(item, index) in audits" :key="index" @click="auditPatient(item.audit_id)">
                  <v-list-tile-content >
                    <v-list-tile-title >{{ item.physician_name }} - {{ item.patient_name }} - {{ item.service_date }}</v-list-tile-title>
                  </v-list-tile-content>
                  <v-list-tile-action>
                    <v-icon color="grey darken-1">edit</v-icon>
                  </v-list-tile-action>
                </v-list-tile>             
            </v-list>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
  <!-- <pre>{{ currentUser }}</pre>
  <pre>{{ audits }}</pre> -->
</div>
</template>

<script>
import Axios from "axios";
import Config from "../config";
import Router from "../router";

const defaultState = {
  audits: [
    {
      audit_id: 1,
      auditor: 1,
      audit_date: "",
      audit_type: "",
      npi: "1790762623",
      physician_name: "Aaron, Michael",
      patient_name: "Joens, Tom",
      dob: "12-19-2000",
      mrn: "11111",
      encounter: "1111111111",
      service_date: "09-02-2018",
      facility: "",
      payor: "",
      icd10_billed: [1, 2, 3],
      icd10_audited: "",
      charge_correction_sent_date: "",
      audit_results_communicated_date: "",
      audit_completion_date: "",
      audit_score: ""
    },
    {
      audit_id: 2,
      auditor: 1,
      audit_date: "",
      audit_type: "",
      npi: "1790762623",
      physician_name: "Aaron, Michael",
      patient_name: "Smyth, John",
      dob: "06-13-2000",
      mrn: "22222",
      encounter: "2222222222",
      service_date: "09-05-2018",
      facility: "",
      payor: "",
      icd10_billed: [1, 2, 3],
      icd10_audited: "",
      cpt: "91020",
      charge_correction_sent_date: "",
      audit_results_communicated_date: "",
      audit_completion_date: "",
      audit_score: ""
    }
  ]
};

export default {
  name: "Main",
  components: {},
  data: () => ({
    audits: []
  }),
  async mounted() {
    //this.$store.dispatch("FETCH_AUDITS", { auditor: this.currentUser.id });
    const result = await Axios.get(Config.apiRoot + "/audit/auditor/" + this.currentUser.id);
    this.audits = result.data;
  },
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    }
  },
  methods: {
    auditPatient(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
